import Vue from 'vue'
import VueResource from 'vue-resource'
import THREE from 'three'
import style from './style.css'
import fontello from '../fontello.css'

Vue.use(VueResource)

const AlbumCube = Vue.extend({
  template: `<div class={{style.imgwrap}}>
      <div class={{style.row}}>
        <div class={{style.scroller}}>
          <img v-for="item in allAlbums" class={{style['img-inner']}} v-bind:src=item.image v-on:click.prevent="select(item, $index, $event)" />
          <!--div v-for="item in allAlbums" class={{style['img-inner']}} v-bind:style="{ backgroundImage: 'url(' + item.image + ')' }" v-on:click.prevent="select(item, $index, $event)"></div-->
        </div>
      </div>
      <i class="{{fontello['icon-left-open']}} {{style['to-left']}} {{style['arrow-left']}}" v-on:click.prevent="toleft($event)"></i>
      <i class="{{fontello['icon-right-open']}} {{style['to-right']}} {{style['arrow-right']}}" v-on:click.prevent="toright($event)"></i>
    </div>
    <div class={{style.albumInfo}}>
      <p v-bind:style="{color: color1}">{{artist}}</p>
      <p v-bind:style="{color: color2}">{{title}}</p>
    </div>`,
  data() {
    return {
      style,
      fontello,
      artist: '',
      title: '',
      allAlbums: [],
      location: 0
    }
  },
  ready() {

  },
  methods: {
    componentToHex: function(c) {
      var hex = c.toString(16)
      return hex.length == 1 ? "0" + hex : hex
    },
    rgbToHex: function(rgb){
      let c = ''
      for(let i=0; i<3; i++){
        c += this.componentToHex(rgb[i])
      }
      return c
    },
    select: function(album, index, event){
      let width = (index-1)*40
      let translateString = `translate3d(-${width}%, 0, 0)`
      event.target.parentElement.style.transform = translateString
      event.target.classList.toggle(style['current'])

      let colorThief = new ColorThief()

      let dominant = colorThief.getColor(event.target);
      let colors = colorThief.getPalette(event.target, 2)
      console.log(dominant,colors);

      document.body.style.background = '#' + this.rgbToHex(dominant)
      document.body.style.color = '#' + this.rgbToHex(colors[1])

    },
    toright: function(event){
      this.location -= 20
      let translateString = `translate3d(${this.location}%, 0, 0)`
      document.querySelector('.'+style.scroller).style.transform = translateString
    },
    toleft: function(event){
      this.location += 20
      let translateString = `translate3d(${this.location}%, 0, 0)`
      document.querySelector('.'+style.scroller).style.transform = translateString
    }
  },
  route: {
    activate: function(transition){
      let that = this
      let APIkey = "4dff88a0423651b3570253b10b745b2c",
        Limit = 10,
        Page = 1,
        User = "fakelbst"

      Vue.http.get("http://ws.audioscrobbler.com/2.0/", {
        params: {
          method: 'user.gettopalbums',
          format: 'json',
          user: User,
          api_key: APIkey,
          limit: Limit,
          page: 1
        }
      }).then((d) => {
        let datas = d.json()
        let albums = datas.topalbums.album
        let src = albums[0].image[3]['#text']
        for(let i=0,j=albums.length; i<j; i++){
            let title = albums[i].name.split(' ').join('-')
            let ext = albums[i].image[3]['#text'].split('.').pop()
            that.allAlbums.push({file: title + '.' + ext, title: albums[i].name, artist: albums[i].artist.name, image: '/static/albums/' + title + '.' + ext})
        }
      })
      transition.next()
    }
  }

})

export default AlbumCube



